	module.exports = {"data":{"markdownRemark":{"html":"<p>When an item has its quantity drop to zero, communicate with a separate service that will then ‘Order’ more stock and then inform the API that more stock has been delivered. The API will then record the new quantity of stock. </p>\n<p>The new service should communicate with your API using coupled, synchronous calls. I.e. TCP or REST calls. (This may feel wrong, and it IS. We will fix this in another step).\nTry to make your calls transactional so your services are in sync and try to consider any failures. </p>\n<p>As in the previous steps, use TDD and your choice of either SOLID or CQRS.\nThe new service can be an API or a console application. That’s up to you.</p>\n<p><a href=\"../shopping4\">&#x3C; Prev</a> | <a href=\"../shopping6\">Next ></a></p>","frontmatter":{"path":"/shopping5","title":"Shopping Cart 5: Distributed"}}},"pathContext":{}}

